import { css } from "emotion";
// import Link from "next/link";
import { Wrapper } from "reactism/elements";
import { Link } from "Routes";
import { withRouter } from "next/router";

const HeaderOne = () => {
  const baseStyle = css`
    background-color: #fff;
    border-bottom: 2px solid rgba(0, 0, 0, 0.1);
    .wrapper {
      display: grid;
      grid-template-columns: 1fr 1fr 1fr;
      grid-template-rows: 35px;
      justify-items: stretch;
      align-items: center;
      a {
        display: block;
        text-decoration: none;
        color: #000;
        margin-left: 10px;
        font-size: 12px;
      }
      div:nth-child(2) {
        justify-self: center;
        position: relative;
        .wrapLogo {
          width: 140px;
          height: 140px;
          border-radius: 50%;
          position: absolute;
          background-color: #fff;
          box-shadow: 0px 7px 0px -5px rgba(0, 0, 0, 0.1);
          top: -2.7em;
          left: -4.5em;
          z-index: 3;
          display: grid;
          justify-items: center;
          align-items: center;
          img {
            width: 65%;
          }
          /* &::before {
            content: '';
            width: 140px;
            height: 140px;
            border-radius: 50%;
            position: absolute;
            background-color: transparent;
            border: 2px solid red;
          } */
        }
      }
      div:nth-child(3) {
        justify-self: end;
        img {
          float: left;
          width: 25px;
          margin-top: -2px;
        }
        a {
          float: left;
        }
      }
    }

    @media (max-width: 700px) {
      display: none;
    }
  `;

  return (
    <nav className={baseStyle}>
      <Wrapper pl={7} pr={7}>
        <div className="wrapper">
          <div>
            <Link route="/">
              <a>KEJARI BARRU</a>
            </Link>
          </div>
          <div className="logo">
            <div className="wrapLogo">
              <img src="/static/logo_kejari_color.svg" alt="logo" />
            </div>
          </div>
          <div>
            <img src="/static/icon/ic_hubungi_kami.svg" alt="icon contact" />
            <Link href="">
              <a>HUBUNGI KAMI</a>
            </Link>
          </div>
        </div>
      </Wrapper>
    </nav>
  );
};

export default withRouter(HeaderOne);
